body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top: 20px;
}

.success .modal-content {
  background: #55d0f9;
}

.danger .modal-content {
  background: #ed3a4a;
}

.title-block {
  color: #fff;
}

.title-block .icon {
  font-size: 34px;
}

.title-block .title {
  font-size: 30px;
  margin-left: 10px;
}

.success .btn-outline-success {
  background: #fff;
  border-color: #fff;
  color: #55d0f9;
}

.danger .btn-outline-danger {
  background: #fff;
  border-color: #fff;
  color: #ed3a4a;
}

.body {
  color: #fff;
  font-size: 16px;
}

.btn-custom {
  color: #fff;
  border-color: #fff;
  margin-right: 5px;
}

.btn-custom:hover {
  color: #fff;
}

